<template>
  <x-header :left-options="{showBack: true}"></x-header>
  <panel v-if="receiveGroup && status === 2" :list="[{title: '',desc: ' ' + receiveGroup + ' ' + receiveContact}]" type="2"></panel>
  <panel v-if="status === 3" :list="[{title: ''}]" type="2"></panel>
  <cell title="" :value="name"></cell>
  <cell title=""><badge :text="group"></badge></cell>
  <cell title="" :value="$root.getStatus(status)"></cell>
  <cell title="">
    <rater :value.sync="rate" slot="value" star="" active-color="#FF9900" :margin="5" disabled></rater>
  </cell>
  <cell title="">{{ lifetime }} </cell>
  <cell title="">{{ price }}</cell>
  <cell title="" :value="location"></cell>
  <cell title="" :value="itemusername"></cell>
  <cell title="" :value="contact"></cell>
  <card :header="{title:''}">
    <p slot="content" class="card-padding">{{ description }}</p>
  </card>
  <card v-show="image" :header="{title:''}">
    <img :src="image" slot="content" style="width:100%;height:auto" />
  </card>
  <template v-if="user === itemuser">
    <template v-if="status === 0">
      <x-button type="primary" text="" @click="finish"></x-button>
      <x-button type="default" text="" @click="receive"></x-button>
    </template>
  </template>
  <template v-else>
    <template v-if="status === 0 || (isAdmin && status === 1)">
      <x-button type="primary" text="" @click="contactMethods"></x-button>
    </template>
  </template>
  <template v-if="isAdmin && status === 1">
    <x-button type="primary" text="" @click="showDialog = true"></x-button>
    <x-button type="warn" text="" @click="showRefuse = true"></x-button>
  </template>
  <template v-if="isAdmin || user === itemuser">
    <x-button type="default" text="" @click="editMethods"></x-button>
    <x-button type="warn" text="" @click="showConfirm = true"></x-button>
  </template>
  <x-button type="default" text="" @click="back"></x-button>
  <divider></divider>
  <divider></divider>
  <divider></divider>
  <toast :show.sync="showSuccess"></toast>
  <toast :show.sync="showCancel" type="cancel"></toast>
  <confirm :show.sync="showConfirm" title="" @on-confirm="delitem" confirm-text="" cancel-text=""><p style="text-align:center;">?</p></confirm>
  <div v-if="status === 1">
    <dialog :show.sync="showDialog" class="dialog-demo">
      <p class="dialog-title"></p>
      <group>
        <selector placeholder="" title="" :options="grouplist" :value.sync="receiveGroup"></selector>
      </group>
      <x-button type="default" text="" @click="accept"></x-button>
      <span class="vux-close" @click="showDialog=false"></span>
    </dialog>
  </div>
  <div v-if="status === 1">
    <dialog :show.sync="showRefuse" class="dialog-demo">
      <p class="dialog-title"></p>
      <x-input title="" name="name" placeholder="" :value.sync="reason"></x-input>
      <x-button type="default" text="" @click="refuse"></x-button>
      <span class="vux-close" @click="showRefuse=false"></span>
    </dialog>
  </div>
</template>

<script>
import { Panel, Dialog, Cell, XHeader, XInput, XButton, Toast, Group, Card, Confirm, Rater, Divider, Selector, Badge } from 'vux/src/components'

export default {
  components: {
    Panel,
    Dialog,
    Cell,
    XHeader,
    XInput,
    XButton,
    Toast,
    Group,
    Card,
    Confirm,
    Rater,
    Divider,
    Selector,
    Badge
  },
  data () {
    return {
      // note: changing this line won't causes changes
      // with hot-reload because the reloaded component
      // preserves its current state and we are modifying
      // its initial state.
      itemid: '',
      name: '',
      status: 0,
      description: '',
      contact: '',
      location: '',
      itemuser: '',
      itemusername: '',
      image: '',
      rate: 5,
      price: 1,
      lifetime: 0,
      group: '',
      isDisabled: false,
      showCancel: false,
      showSuccess: false,
      isLogin: false,
      showConfirm: false,
      user: '',
      isAdmin: false,
      showDialog: false,
      showRefuse: false,
      reason: '',
      grouplist: ['', '()', '', ''],
      contactList: ['400-878-5157', '022-67160791', '022-27341983', '13642042577'],
      receiveGroup: '',
      receiveContact: ''
    }
  },
  ready () {
    if (window.user) {
      this.isAdmin = window.userobj.get('admin')
    }
    let Items = window.Bmob.Object.extend('items')
    // 
    let query = new window.Bmob.Query(Items)
    // objectId
    query.get(this.$route.params.id).then((object) => {
    // get
      this.itemid = object.id
      this.name = object.get('name')
      this.status = object.get('status')
      this.description = object.get('description')
      this.contact = object.get('contact')
      this.location = object.get('location')
      this.itemuser = object.get('user').id
      this.rate = object.get('rate')
      this.price = object.get('price')
      this.lifetime = object.get('lifetime')
      this.group = object.get('group')
      if (object.get('receiveGroup')) {
        this.receiveGroup = object.get('receiveGroup').split(' ')[0]
        this.receiveContact = object.get('receiveGroup').split(' ')[1]
      }
      query = new window.Bmob.Query(window.Bmob.User)
      query.get(object.get('user').id).then((man) => {
        this.itemusername = man.get('username')
      })
      if (object.get('image')) {
        window.Bmob.Image.thumbnail({'image': object.get('image')._url, 'mode': 0, 'quality': 100, 'width': 800}).then((obj) => {
          this.image = 'http://file.bmob.cn/' + obj.url
        })
      }
    }, (object, error) => {
      console.log(': ' + error.code + ' ' + error.message)
    })
    if (window.user) {
      this.user = window.user
    } else {
      this.user = false
    }
  },
  methods: {
    finish: function () {
      let Items = window.Bmob.Object.extend('items')
      // 
      let query = new window.Bmob.Query(Items)
      // objectId
      query.get(this.$route.params.id).then((object) => {
      // get
        object.set('status', 3)
        object.save()
        this.status = 3
        this.showSuccess = true
        // this.$router.go('/user')
        window.Bmob.Cloud.run('sendmsg', {'from': 'YZeiFFFI', 'to': object.get('user').id, 'content': ' ' + this.name + ' '}).then((result) => {
          console.log(result)
        }, (error) => {
          console.log('Error!' + error)
        })
        window.Bmob.Cloud.run('sendmsg', {'from': 'YZeiFFFI', 'to': 'YZeiFFFI', 'content': ' ' + this.name + ' '}).then((result) => {
          console.log(result)
        }, (error) => {
          console.log('Error!' + error)
        })
        console.log('create object success, object id:' + object.id)
      }, (object, error) => {
        this.showCancel = true
        console.log('Error!' + error.code + ' ' + error.message)
      })
    },
    receive: function () {
      let Items = window.Bmob.Object.extend('items')
      // 
      let query = new window.Bmob.Query(Items)
      // objectId
      query.get(this.$route.params.id).then((object) => {
      // get
        object.set('status', 1)
        object.save()
        this.showSuccess = true
        this.status = 1
        // this.$router.go('/user')
        window.Bmob.Cloud.run('sendmsg', {'from': 'YZeiFFFI', 'to': object.get('user').id, 'content': ' ' + this.name + ' '}).then((result) => {
          console.log(result)
        }, (error) => {
          console.log('Error!' + error)
        })
        console.log('create object success, object id:' + object.id)
      }, (object, error) => {
        this.showCancel = true
        console.log('Error!' + error.code + ' ' + error.message)
      })
    },
    delitem: function () {
      let Items = window.Bmob.Object.extend('items')
      // 
      let query = new window.Bmob.Query(Items)
      // objectId
      query.get(this.$route.params.id).then((object) => {
      // get
        object.destroy((object) => {
          this.showSuccess = true
          this.$router.go('/')
          window.Bmob.Cloud.run('sendmsg', {'from': 'YZeiFFFI', 'to': object.get('user').id, 'content': ' ' + this.name + ' '}).then((result) => {
            console.log(result)
          }, (error) => {
            console.log('Error!' + error)
          })
          console.log('success, object id:' + object.id)
        }, (object, error) => {
          this.showCancel = true
          console.log('Error!' + error.code + ' ' + error.message)
        })
      }, (object, error) => {
        this.showCancel = true
        console.log('Error!' + error.code + ' ' + error.message)
      })
    },
    accept: function () {
      let Items = window.Bmob.Object.extend('items')
      // 
      let query = new window.Bmob.Query(Items)
      // objectId
      query.get(this.$route.params.id).then((object) => {
      // get
        object.set('status', 2)
        this.showSuccess = true
        this.status = 2
        // this.$router.go('/user')
        for (let i = 0; i < this.grouplist.length; i++) {
          if (this.receiveGroup === this.grouplist[i]) {
            this.receiveContact = this.contactList[i]
          }
        }
        object.set('receiveGroup', this.receiveGroup + ' ' + this.receiveContact)
        object.save()
        window.Bmob.Cloud.run('sendmsg', {'from': 'YZeiFFFI', 'to': object.get('user').id, 'content': ' ' + this.name + '  ' + this.receiveGroup + '' + this.receiveContact + '72'}).then((result) => {
          console.log(result)
        }, (error) => {
          console.log('Error!' + error)
        })
        this.showDialog = false
        console.log('create object success, object id:' + object.id)
      }, (object, error) => {
        this.showCancel = true
        console.log('Error!' + error.code + ' ' + error.message)
      })
    },
    refuse: function () {
      let Items = window.Bmob.Object.extend('items')
      // 
      let query = new window.Bmob.Query(Items)
      // objectId
      query.get(this.$route.params.id).then((object) => {
      // get
        object.set('status', 0)
        object.save()
        this.showSuccess = true
        this.status = 0
        // this.$router.go('/user')
        window.Bmob.Cloud.run('sendmsg', {'from': 'YZeiFFFI', 'to': object.get('user').id, 'content': ' ' + this.name + '  ' + this.reason + ''}).then((result) => {
          console.log(result)
        }, (error) => {
          console.log('Error!' + error)
        })
        this.showRefuse = false
        console.log('create object success, object id:' + object.id)
      }, (object, error) => {
        this.showCancel = true
        console.log('Error!' + error.code + ' ' + error.message)
      })
    },
    back: function () {
      this.$router.go('/')
    },
    contactMethods: function () {
      this.$router.go('/chat/' + this.itemuser + '/' + this.$route.params.id)
    },
    editMethods: function () {
      this.$router.go('/edit/' + this.$route.params.id)
    }
  }
}
</script>

<style lang="less" scoped>
@import '../styles/close';
.card-padding {
  padding: 15px;
}
.dialog-demo {
  .weui_dialog{
    border-radius: 8px;
    padding-bottom: 8px;
  }
  .dialog-title {
    line-height: 30px;
    color: #666;
  }
  .img-box {
    height: 350px;
    overflow: hidden;
  }
  .vux-close {
    margin-top: 8px;
    margin-bottom: 8px;
  }
}
</style>


