<template>
  <x-header :left-options="{showBack: false}"></x-header>
  <group>
    <x-input title="" name="name" placeholder="" :value.sync="name"></x-input>
    <selector placeholder="" title="" :options="grouplist" :value.sync="group"></selector>
    <group title="">
      <x-textarea :max="200" :rows="6" placeholder="
    1.
    2.
    3.
    4." :value.sync="description"></x-textarea>
    </group>
    <cell title="">
      <rater :value.sync="rate" slot="value" star="" active-color="#FF9900" :margin="5"></rater>
    </cell>
    <x-number :value.sync="lifetime" title="" :min="0" :max="1000" :step="0.5"></x-number>
    <x-number :value.sync="price" title="" :min="0.5" :step="0.5"></x-number>
    <address title="" :value.sync="location" :list="addressData" placeholder=""></address>
    <x-input title="" name="contact" placeholder="" :value.sync="contact" keyboard="number" is-type="china-mobile"></x-input>
    <cell title="">
      <input id="fileUpload" type="file" accept="image/*" value="" />
    </cell>
    <switch title="" :value.sync="recycle"></switch>
  </group>
  <group>
    <x-button type="primary" :text="btnText" :disabled="isDisabled" @click="click"></x-button>
    <x-button type="default" text="" @click="back"></x-button>
  </group>
  <divider></divider>
  <divider></divider>
  <divider></divider>
  <toast :show.sync="showSuccess"></toast>
  <toast :show.sync="showCancel" type="cancel"></toast>
  <toast :show.sync="showEmpty" type="cancel"></toast>
</template>

<script>
import { XHeader, XInput, XTextarea, XButton, Toast, Group, Address, AddressChinaData, Cell, Rater, XNumber, Selector, Switch, Divider } from 'vux/src/components'
import value2name from 'vux/src/filters/value2name'
import $ from 'jquery'

export default {
  components: {
    XHeader,
    XInput,
    XTextarea,
    XButton,
    Toast,
    Group,
    Address,
    Cell,
    Rater,
    XNumber,
    Selector,
    Switch,
    Divider
  },
  data () {
    return {
      // note: changing this line won't causes changes
      // with hot-reload because the reloaded component
      // preserves its current state and we are modifying
      // its initial state.
      btnText: '',
      name: '',
      description: '',
      contact: '',
      rate: 5,
      price: 1,
      lifetime: 0,
      location: ['120000', '120100', '120101'],
      group: '',
      grouplist: ['', '', '', '', '', ''],
      isDisabled: false,
      showCancel: false,
      showSuccess: false,
      showEmpty: false,
      addressData: AddressChinaData,
      files: [],
      recycle: false
    }
  },
  ready () {
    if (!window.user) {
      this.$router.go('/user')
    }
    if (this.$route.params.id) {
      let Items = window.Bmob.Object.extend('items')
      let query = new window.Bmob.Query(Items)
      query.get(this.$route.params.id).then((object) => {
        this.name = object.get('name')
        this.status = object.get('status')
        this.description = object.get('description')
        this.contact = object.get('contact')
        this.itemuser = object.get('user').id
        this.rate = object.get('rate')
        this.price = object.get('price')
        this.lifetime = object.get('lifetime')
        this.group = object.get('group')
      })
    }
  },
  methods: {
    click: function () {
      if (this.name === '' || this.description === '' || this.contact === '' || this.group === '') {
        this.showEmpty = true
        console.log('Empty')
      } else {
        this.isDisabled = true
        this.btnText = '...'
        let Items = window.Bmob.Object.extend('items')
        if (this.$route.params.id) {
          var query = new window.Bmob.Query(Items)
          query.get(this.$route.params.id).then((items) => {
            this.saveitem(items)
          })
        } else {
          let items = new Items()
          this.saveitem(items)
        }
      }
    },
    saveitem: function (items) {
      if (this.location !== ['120000', '120100', '120101']) {
        items.set('location', value2name(this.location, AddressChinaData))
      }
      items.set('user', window.userobj)
      items.set('name', this.name)
      items.set('description', this.description)
      items.set('contact', this.contact)
      items.set('rate', this.rate)
      items.set('price', this.price)
      items.set('lifetime', this.lifetime)
      items.set('group', this.group)
      if (this.recycle) {
        items.set('status', 2)
      } else {
        items.set('status', 0)
      }
      let fileUploadControl = $('#fileUpload')[0]
      let file = fileUploadControl.files[0]
      if (fileUploadControl.files.length > 0) {
        let name = file.name
        let fileobj = new window.Bmob.File(name, file)
        items.set('image', fileobj)
      }
      items.save(null).then((object) => {
        this.showSuccess = true
        this.isDisabled = false
        this.btnText = ''
        this.$router.go('/')
        console.log('create object success, object id:' + object.id)
      },
      (model, error) => {
        this.showCancel = true
        this.isDisabled = false
        this.btnText = ''
        console.log('Error!' + error)
      })
    },
    back: function () {
      this.$router.go('/')
    }
  }
}
</script>


